<template>
  <div class="goodslist">
    <Card>
      <div slot="title">商品列表</div>
      <div slot="content">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="商品ID:">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label="所属分类:">
                  <span>{{ props.row.category }}</span>
                </el-form-item>
                <el-form-item label="商品图片:">
                  <span>{{ props.row.imgUrl }}</span>
                </el-form-item>
                <el-form-item label="商品评价:">
                  <span>{{ props.row.common }}</span>
                </el-form-item>
                <el-form-item label="商品描述:">
                  <span>{{ props.row.goodsDesc }}</span>
                </el-form-item>
                <el-form-item label="商品名称:">
                  <span>{{ props.row.name }}</span>
                </el-form-item>
                <el-form-item label="商品价格:">
                  <span>{{ props.row.price }}</span>
                </el-form-item>
                <el-form-item label="创建时间:">
                  <span>{{ props.row.ctime }}</span>
                </el-form-item>
                <el-form-item label="商品销量:">
                  <span>{{ props.row.sellCount }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label="商品名称" prop="tableData.name">
            <template slot-scope="scope">{{ scope.row.name }}</template>
          </el-table-column>
          <el-table-column label="所属分类" prop="tableData.category">
            <template slot-scope="scope">{{ scope.row.category }}</template>
          </el-table-column>
          <el-table-column label="商品价格" prop="tableData.price">
            <template slot-scope="scope">{{ scope.row.price }}</template>
          </el-table-column>
          <!-- 商品图片 -->
          <el-table-column label="商品图片" prop="baseUrl+imgUrl">
            <template slot-scope="scope"
              ><el-image
                style="width: 50px; height: 50px"
                :src="baseUrl + scope.row.imgUrl"
              ></el-image
            ></template>
          </el-table-column>
          <el-table-column label="商品描述" prop="tableData.goodsDesc">
            <template slot-scope="scope">{{ scope.row.goodsDesc }}</template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" @click="edit(scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="del(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[1, 3, 5, 10]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </Card>
    <!-- 弹窗 -->
    <el-dialog title="修改商品" :visible.sync="editFormVisible">
      <el-form ref="editForm" :model="editForm" label-width="80px" size="mini">
        <el-form-item label="商品名称">
          <el-input v-model="editForm.name" class="goodsname"></el-input>
        </el-form-item>
        <el-form-item label="商品分类">
          <el-select v-model="editForm.category" placeholder="请选择添加商品">
            <el-option
              v-for="item in typeArr"
              :key="item.cateName"
              :label="item.cateName"
              :value="item.cateName"
            ></el-option>
          </el-select>
        </el-form-item>
        <!--商品价格  -->
        <el-form-item label="商品价格">
          <el-input-number
            v-model="editForm.price"
            :min="1"
            label="价格"
          ></el-input-number>
        </el-form-item>
        <!-- 商品图片 -->
        <el-form-item label="上传图片">
          <el-upload
            class="avatar-uploader"
            action="http://127.0.0.1:5000/goods/goods_img_upload"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
          >
            <img
              v-if="imgUrl"
              :src="baseUrl + editForm.imgUrl"
              class="avatar"
            />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <!-- 商品描述 -->
        <el-form-item label="商品描述">
          <el-input
            type="textarea"
            :rows="3"
            placeholder="请输入内容"
            v-model="editForm.goodsDesc"
          >
          </el-input>
        </el-form-item>
        <!--  -->
        <el-form-item size="large">
          <el-button type="primary" @click="editBtn">修改商品</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
//引入卡片
import Card from "@/components/Card";
//引入接口
import { api_getGoodsList, api_serTypeName, api_editGoods, api_delGoods } from "@/api/goods";
//引入时间格式模块
import moment from "moment"
export default {
  components: {
    Card,
  },
  data() {
    return {
      baseUrl: "http://127.0.0.1:5000/upload/imgs/goods_img/",
      currentPage: 1, //当前页面
      pageSize: 3,
      total: 1,
      imgUrl: "",
      tableData: [],
      editFormVisible: false,
      // 弹窗数据数组
      editForm: {},
      //商品类型数组
      typeArr: [],
    };
  },
  methods: {
    //定义渲染列表函数
    async getGoodsList() {
      let { total, data } = await api_getGoodsList({
        currentPage: this.currentPage,
        pageSize: this.pageSize,
      });
      if(!data.length &&this.currentPage !=1){
        this.currentPage -= 1
        this.getGoodsList()
      }
      data.forEach((item) => {
        this.imgUrl = item.imgUrl;
        //修改时间格式
        item.ctime = moment(item.ctime).format("YYY-MM-DD HH:mm:ss")
      });
      this.tableData = data;
      this.total = total;
    },
    //编辑按钮
    async edit(row) {
      //点击弹出修改窗
      this.editFormVisible = true;
      //将列表内容赋值
      this.editForm = row;
      //引入商品类型列表
      let { categories } = await api_serTypeName();
      this.typeArr = categories;
    },
    //删除按钮
    async del(row) {
      let {code} = await  api_delGoods({
        id:row.id
      })
      if(code==0){
        this.getGoodsList()
      }

    },
    //当条数变化时触发
    handleSizeChange(val) {
      this.pageSize = val;
      this.getGoodsList();
    },
    // 当前页数变化时
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getGoodsList();
    },
    //图片成功上传成功
    handleAvatarSuccess(val) {
      this.editForm.imgUrl = val.imgUrl
    },
    // 点击修改商品按钮
    async editBtn() {
      let { name, category, price, imgUrl, goodsDesc, id } = this.editForm;
      let { code } = await api_editGoods({
        name,
        category,
        price,
        imgUrl,
        goodsDesc,
        id,
      });
      if (code == 0) {
        //关闭弹窗
        this.editFormVisible = false;
        //重新渲染页面
        this.getGoodsList();
      }
    },
  },
  created() {
    this.getGoodsList();
  },
};
</script>

<style lang="less" scoped>
.goodslist {
  background: #fff;
  user-select: none;
  border-radius: 4px;
  overflow: hidden;
  /deep/ h3 {
    border-bottom: 1px solid #eff1f7;
    padding: 10px;
  }
  .el-pagination {
    margin: 10px;
  }
  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }
}
</style>