//引入请求库文件
import request from "@/units/request"

//添加分类
export function api_addType(params){
    return request.post("/goods/addcate",params)
}
//获取分类
export function api_getType(params){
    return request.get("/goods/catelist",params)
}
//删除分类
export function api_delType(params){
    return request.get("/goods/delcate",params)
}
//修改分类
export function api_editType(params){
    return request.post("/goods/editcate",params)
}
// ***********************
//查询所有分类名称
export function api_serTypeName(){
    return request.get("/goods/categories",)
}
//商品图片上传
export function api_imgUpdata(params){
    return request.post("/goods/goods_img_upload",params)
}
//添加商品
export function api_addGoods(params){
    return request.post("/goods/add",params)
}
//获取商品列表
export function api_getGoodsList(params){
    return request.get("/goods/list",params)
}
//删除商品
export function api_delGoods(params){
    return request.get("/goods/del",params)
}
//修改商品
export function api_editGoods(params){
    return request.post("/goods/edit",params)
}



